import { compose } from 'redux';
import { BrowserRouter, Route, Routes, Navigate } from 'react-router-dom';

import { initializeStore } from 'store';
import { breakpoints, setBreakpoint } from 'store/adaptivity';

import ROUTES from 'constants/routes';

import CONTENT from 'constants/content';

import { withAdaptivity, withReduxStore } from 'enhancers';

import { PageLayout } from 'components';
import { RESULTS } from 'constants/const';

import { InitialPage } from './InitialPage';
import { ConfirmRedirect } from './ConfirmRedirect';
import { WaitingPushPage } from './WaitingPushPage';
import { Result } from './Result';
import { ErrorPage } from './ErrorPage';
import { EnterCode } from './EnterCode';

const { RESULT, AUTH_SUCCESS } = CONTENT;

const AppComponent = () => (
  <div style={{ minHeight: '100vh' }}>
    <BrowserRouter>
      <PageLayout>
        <Routes>
          <Route path={ROUTES.INITIAL} element={<InitialPage />} />
          <Route path={ROUTES.WAITING} element={<WaitingPushPage />} />
          <Route path={ROUTES.CONFIRM} element={<ConfirmRedirect />} />
          <Route path={`${ROUTES.RESULT}/*`}>
            <Route
              path={RESULTS.SUCCESS}
              element={<Result content={RESULT[RESULTS.SUCCESS]} />}
            />
            <Route
              path={RESULTS.LINK_EXPIRED}
              element={<Result content={RESULT[RESULTS.LINK_EXPIRED]} />}
            />
          </Route>
          <Route
            path={ROUTES.SUCCESS}
            element={<Result content={AUTH_SUCCESS} />}
          />
          <Route path={ROUTES.ENTER_CODE} element={<EnterCode />} />
          <Route path={ROUTES.ERROR} element={<ErrorPage />} />
          <Route path="*" element={<Navigate to={ROUTES.ERROR} />} />
        </Routes>
      </PageLayout>
    </BrowserRouter>
  </div>
);

export const App = compose(
  withReduxStore(initializeStore({ name: 'MOBID::IDGW' })),
  withAdaptivity({ breakpoints, setBreakpoint }),
)(AppComponent);
