import { useState, useRef, useEffect } from 'react';
import { Button, Text, Title, Tooltip } from 'ui';

import CONTENT from 'constants/content';

import classNames from 'classnames/bind';
import { ThemedImage } from 'components';

import { templateParser } from 'utils/format-string';

import { useTimer } from 'hooks/useTimer';

import styles from './styles.pcss';

const cx = classNames.bind(styles);

const { WAITING_PUSH_PAGE } = CONTENT;

const PHONE_NUMBER = '+7 999 233 34-34';

export const WaitingPushPage = () => {
  const showSendSMS = useRef(false);

  const { timeLeft, onReset } = useTimer({
    timeout: WAITING_PUSH_PAGE.TIMEOUT,
  });

  useEffect(() => {
    if (
      !showSendSMS.current &&
      timeLeft ===
        WAITING_PUSH_PAGE.TIMEOUT - WAITING_PUSH_PAGE.SENDSMS_BUTTON_TIMEOUT
    ) {
      showSendSMS.current = true;
    }
  }, [timeLeft, showSendSMS.current]);

  const handleRetry = () => {
    console.log('retry');
    onReset();
  };

  const handleSendSMS = () => {
    console.log('SendSMS');
  };

  return (
    <>
      <Title>{WAITING_PUSH_PAGE.TITLE}</Title>

      {WAITING_PUSH_PAGE.DESCRIPTION && (
        <Text className="mart-xs">{WAITING_PUSH_PAGE.DESCRIPTION}</Text>
      )}

      <div className={cx('info')}>
        <div className={cx('info-left')}>
          <Text isBold500 size="m">
            {WAITING_PUSH_PAGE.INFO_TEXT}
            <div>{PHONE_NUMBER}</div>
          </Text>
          <div>
            <Tooltip
              text={WAITING_PUSH_PAGE.ABOUT_PUSH_TOOLTIP}
              label={
                <Text className={cx('about-push-label')} size="regular">
                  {WAITING_PUSH_PAGE.ABOUT_PUSH}
                </Text>
              }
              className={cx('about-push')}
              position="left"
              width={250}
            />
          </div>
        </div>
        <ThemedImage name={WAITING_PUSH_PAGE.ICON} className="marb-xse" />
      </div>

      <Button className="mart-s" disabled={timeLeft} onClick={handleRetry}>
        {templateParser(WAITING_PUSH_PAGE.SUBMIT_BUTTON_TEXT, {
          seconds: timeLeft ? ` (${timeLeft})` : '',
        })}
      </Button>

      {showSendSMS.current && (
        <Button
          styleType="secondary"
          className="mart-s"
          onClick={handleSendSMS}
        >
          {WAITING_PUSH_PAGE.ENTER_FROM_SMS}
        </Button>
      )}
    </>
  );
};
